<template>
  <v-dialog
    v-model="isModalOpen"
    persistent
    max-width="500px"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        color="primary"
        dark
        type="submit"
        class="float-right"
        v-bind="attrs"
        v-on="on"
      >
        <v-icon left>
          mdi-plus
        </v-icon>
        <slot />
      </v-btn>
    </template>
    <v-card class="pa-4">
      <v-form
        @submit.prevent="onSubmit"
        class="custom-form"
        ref="form"
      >
        <v-card-title>
          <span class="headline">{{ title }}</span>
        </v-card-title>
        <v-container>
          <form-row :larger-key="true">
            <template v-slot:key>
              <label class="custom-form-label" :for="'loan-items'">
                Items for loan
              </label>
            </template>
            <template v-slot:value>
              <v-select
                id="loan-items"
                v-model="loanItem"
                :items="loanItems"
                :rules="rules.loanItems"
                dense
                outlined
              ></v-select>
            </template>
          </form-row>
          <form-row :larger-key="true">
            <template v-slot:key>
              <label
                class="custom-form-label"
                :for="'total-quantity'">
                Total quantity
              </label>
            </template>
            <template v-slot:value>
              <v-text-field
                id="total-quantity"
                type="number"
                v-model="totalQuantity"
                :rules="rules.totalQuantity"
                dense
                outlined
              ></v-text-field>
            </template>
          </form-row>
          <form-row :larger-key="true">
            <template v-slot:key>
              <label
                class="custom-form-label"
              >
                Loaned price (excluding tax)
              </label>
            </template>
            <template v-slot:value>
              <div class="float-right">
                {{ 0 | currency }}
              </div>
            </template>
          </form-row>
          <form-row :larger-key="true">
            <template v-slot:key>
              <label
                class="custom-form-label"
              >
                Unit of payment
              </label>
            </template>
            <template v-slot:value>
              <div class="float-right">
                1 Night
              </div>
            </template>
          </form-row>
        </v-container>
        <v-card-actions class="mt-2">
          <v-spacer></v-spacer>
          <v-btn
            color="red accent-4"
            dark
            @click="isModalOpen = false"
          >
            Cancel
          </v-btn>
          <v-btn
            type="submit"
            color="blue darken-4"
            dark
          >
            Retain and add
          </v-btn>
        </v-card-actions>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
import FormRow from '@/components/Form/FormRow'
export default {
  name: 'AddLoanProductsModal',
  components: { FormRow },
  props: ['data'],
  data () {
    return {
      title: 'Registering additional items for loan',
      isModalOpen: false,
      loanItem: 1,
      totalQuantity: 1,
      loanItems: [
        {
          text: 'Human game',
          value: 1
        },
        {
          text: 'Human game 2',
          value: 2
        }
      ],
      rules: {
        loanItems: [
          v => !!v || 'Loan item is required'
        ],
        totalQuantity: [
          v => !!v || 'Total quantity is required',
          v => (v && v > 0) || 'Total quantity must be more than 0'
        ]
      }
    }
  },
  methods: {
    async onSubmit () {
      if (!this.$refs.form.validate()) {
        return
      }

      this.data.loanProducts.push({
        title: this.loanItems.find(x => x.value === this.loanItem).text,
        quantity: this.totalQuantity,
        price: 0,
        paymentUnit: ''
      })

      this.isModalOpen = false
    }
  }
}
</script>

