<template>
  <v-main>
    <v-container fluid  class="pa-0 ma-0 clearfix">
      <add-loan-products-modal :data="data">
        Add
      </add-loan-products-modal>
    </v-container>
    <v-container fluid>
      <v-simple-table class="custom-table">
        <template v-slot:default>
          <thead>
          <tr>
            <th class="text-left">
              Items on loan
            </th>
            <th class="text-left">
              Total quantity
            </th>
            <th class="text-left">
              Loan unit price (including tax)
            </th>
            <th class="text-left">
              Unit of payment
            </th>
            <th class="text-left"> </th>
          </tr>
          </thead>
          <tbody>
          <tr
            v-for="(loanProduct, i) in data.loanProducts"
            :key="i"
          >
            <td>{{ loanProduct.title }}</td>
            <td>
              <v-text-field
                id="application-name"
                v-model="loanProduct.quantity"
                dense
                outlined
                :hide-details="true"
                type="number"
                class="width-60-px"
              ></v-text-field>
            </td>
            <td>{{ loanProduct.price | currency }}</td>
            <td>{{ loanProduct.paymentUnit }}</td>
            <td>
              <v-btn
                text
                color="error"
                class="float-right"
                @click="deleteLoanProduct(i)"
              >
                Delete
              </v-btn>
            </td>
          </tr>
          </tbody>
        </template>
      </v-simple-table>
    </v-container>
  </v-main>
</template>

<script>
import AddLoanProductsModal from './AddItemModal'
export default {
  components: {
    AddLoanProductsModal
  },
  props: ['data'],
  data () {
    return {
      managementScreenDisplayOrderItems: [
        {
          text: '1',
          value: 1
        },
        {
          text: '2',
          value: 2
        },
        {
          text: '3',
          value: 3
        }
      ],
      feeCollectionItems: [
        {
          text: 'All members',
          value: 1
        },
        {
          text: 'Partial members',
          value: 2
        }
      ],
      askCosleeperAgeItems: [
        {
          text: 'Yes',
          value: 1
        },
        {
          text: 'No',
          value: 0
        }
      ],
      pdfPlanFiles: [],
      rules: {
        applicationName: [
          v => !!v || 'Name is required',
          v => (v && v.length >= 3) || 'Name must be more than 3 characters'
        ]
      }
    }
  },
  methods: {
    deleteLoanProduct (index) {
      this.data.loanProducts.splice(index, 1)
    }
  }
}
</script>

<style scoped>

</style>
