<template>
  <v-container fluid>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label">
          Facility ID
        </label>
      </template>
      <template v-slot:value>
        {{ data.id }}
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label">
          Facility type
        </label>
      </template>
      <template v-slot:value>
        TVS facility
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'application-name'">
          Application name (for management)
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-text-field
              id="application-name"
              :rules="rules.applicationName"
              dense
              outlined
            ></v-text-field>
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'screen-display-order'">
          Management screen display order
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-select
              id="screen-display-order"
              :items="managementScreenDisplayOrderItems"
              label="Select screen display order"
              dense
              outlined
            ></v-select>
          </v-col>
        </v-row>
      </template>
    </form-row>

    <form-divider />

    <form-row>
      <template v-slot:key>
        <label class="custom-form-label">
          The application form is available in 
        </label>
      </template>
      <template v-slot:value>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
        <v-icon>
          mdi-minus
        </v-icon>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
      </template>
    </form-row>

    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'residence'">
          Residence
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="10" lg="7">
            <v-text-field
              id="residence"
              dense
              outlined
            ></v-text-field>
          </v-col>
        </v-row>
      </template>
    </form-row>

    <form-row>
      <template v-slot:key>
        <label class="custom-form-label">
          Tel.
        </label>
      </template>
      <template v-slot:value>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
        <v-icon>
          mdi-minus
        </v-icon>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
        <v-icon>
          mdi-minus
        </v-icon>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
      </template>
    </form-row>

    <form-row>
      <template v-slot:key>
        <label class="custom-form-label">
          For the collection form, use FAX
        </label>
      </template>
      <template v-slot:value>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
        <v-icon>
          mdi-minus
        </v-icon>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
        <v-icon>
          mdi-minus
        </v-icon>
        <v-text-field
          dense
          outlined
          class="width-120-px d-inline-block"
        ></v-text-field>
      </template>
    </form-row>
    <form-divider />
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'ask-cosleeper-age'">
          Ask the age of the co-sleeper
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-select
              id="ask-cosleeper-age"
              :items="askCosleeperAgeItems"
              label="Select answer"
              dense
              outlined
            ></v-select>
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'fee-collection'">
          Collection of user fees
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-select
              id="fee-collection"
              :items="feeCollectionItems"
              label="Select fee collection"
              dense
              outlined
            ></v-select>
          </v-col>
          <v-col cols="12" md="7" lg="9">
            No fine for children under 1 year old
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-divider />
    <form-row>
      <template v-slot:key>
        <label class="custom-form-label" :for="'floor-plan-pdf-files'">
          Facility Floor Plan PDF
        </label>
      </template>
      <template v-slot:value>
        <v-row>
          <v-col cols="12" md="5" lg="3">
            <v-file-input
              id="floor-plan-pdf-files"
              v-model="pdfPlanFiles"
              color="deep-purple accent-4"
              counter
              label="Upload Files"
              multiple
              placeholder="Select your files"
              prepend-icon="mdi-paperclip"
              outlined
              dense
              :show-size="1000"
            >
              <template v-slot:selection="{ index, text }">
                <v-chip
                  v-if="index < 2"
                  color="deep-purple accent-4"
                  dark
                  label
                  small
                >
                  {{ text }}
                </v-chip>

                <span
                  v-else-if="index === 2"
                  class="overline grey--text text--darken-3 mx-2"
                >
              +{{ files.length - 2 }} File(s)
            </span>
              </template>
            </v-file-input>
          </v-col>
        </v-row>
      </template>
    </form-row>
    <form-divider />
    <v-row class="form-row">
      <v-col cols="12" sm="4" md="3" lg="2">
        Delete this facility
      </v-col>
      <v-col cols="12" sm="8" md="9" lg="10">
        If there is no reservation for this facility, you can delete it by clicking the button below.
        <br>
        <v-btn
          class="ma-2"
          outlined
          color="pink"
        >
          Delete
        </v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import FormRow from '@/components/Form/FormRow'
import FormDivider from '@/components/Form/FormDivider'

export default {
  components: {
    FormRow,
    FormDivider
  },
  props: ['data'],
  data () {
    return {
      managementScreenDisplayOrderItems: [
        {
          text: '1',
          value: 1
        },
        {
          text: '2',
          value: 2
        },
        {
          text: '3',
          value: 3
        }
      ],
      feeCollectionItems: [
        {
          text: 'All members',
          value: 1
        },
        {
          text: 'Partial members',
          value: 2
        }
      ],
      askCosleeperAgeItems: [
        {
          text: 'Yes',
          value: 1
        },
        {
          text: 'No',
          value: 0
        }
      ],
      pdfPlanFiles: [],
      rules: {
        applicationName: [
          v => !!v || 'Name is required',
          v => (v && v.length >= 3) || 'Name must be more than 3 characters'
        ]
      }
    }
  }
}
</script>

<style scoped>

</style>
